@import url(https://fonts.googleapis.com/css2?family=Kanit:wght@400;500&display=swap);
body, html {
  width: 100%;
  height: 100%;
  zoom: 94%;
  overflow: hidden;
}
.App_app_container__35UVB{
  display: grid;
  grid-template-rows: 30px auto 14px;
  grid-template-areas:
          "hd"
          "mn"
          "ft";
  width: 375px;
  height: 812px;
  background-color: lightgrey;
  background-image: url(/LineOs/static/media/foreground.0b99a867.png);
}

.App_app_block__FKQUm{
  width: 60px;
  height: 60px;
  background-color: violet;
  border-radius: 10px;
  touch-action: none;
}

/* Header */

header{
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  width: 375px;
  padding-top: 8px;
  grid-area: hd;
}

.App_left_header__pjlK5{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 26px;
}
.App_right_header__1lgWT{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 24px;
}

/* Footer */

footer{
  width: 375px;
  margin: 0 auto;
  grid-area: ft;
}
/* Battery Indicator */
:root{
    --color-battery: white
}

.EnvironmentsIndicators_batteryIndicator__2g6Ot{
    position: relative;
    width: 14px;
    height: 6px;
    border-radius: 2px;
    padding: 1px;
    border: 1px solid white;
    border: 1px solid var(--color-battery);
    margin-left: 4px;
}
.EnvironmentsIndicators_batteryIndicator__2g6Ot:after{
    position: absolute;
    content: ' ';
    width: 1.5px;
    height: 3.5px;
    top: calc(50% - 1.75px);
    left: 16.5px;
    border-radius: 1px;
    background-color: white;
    background-color: var(--color-battery);
}
.EnvironmentsIndicators_batteryIndicator__2g6Ot:before{
    position: absolute;

}
.EnvironmentsIndicators_level__ibG1S{
    width: 100%;
    height: 100%;
    background-color: white;
    background-color: var(--color-battery);
    border-radius: 1px;
}



/* CommunicationIndicator */
.EnvironmentsIndicators_communication__19vmv{
    display: flex;
    justify-content: space-between;
    align-items: center;

}

.EnvironmentsIndicators_communicationIndicator__3kLqt{
    width: 11px;
    height: 9px;
    display: flex;
    justify-content: space-around;
    align-items: end;
    margin-right: 4px;
}
.EnvironmentsIndicators_communicationLevel__AY-8R{
    width: 2px;
