.app_container{
  display: grid;
  grid-template-rows: 30px auto 14px;
  grid-template-areas:
          "hd"
          "mn"
          "ft";
  width: 375px;
  height: 812px;
  background-color: lightgrey;
  background-image: url("assets/foreground.png");
}

.app_block{
  width: 60px;
  height: 60px;
  background-color: violet;
  border-radius: 10px;
  touch-action: none;
}

/* Header */

header{
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  width: 375px;
  padding-top: 8px;
  grid-area: hd;
}

.left_header{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 26px;
}
.right_header{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 24px;
}

/* Footer */

footer{
  width: 375px;
  margin: 0 auto;
  grid-area: ft;
}