.quickStart_bar{
    position: absolute;
    left: calc(50% - 173.5px);
    bottom: -90px;
    width: 321px;
    height: 60px;



    border-radius: 26px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 11px 13px;
    transition: all 1s ease-out;
}
.quickStart_bar:before{
    position: absolute;
    content: ' ';
    left: 0;
    width: 321px;
    height: 60px;
    background-color: #fff;
    padding: 11px 13px;
    border-radius: 26px;
    opacity: 0.16;
    backdrop-filter: blur(30px);
}

.quickStart_bar_active{
    bottom: 20px;
}
.quickStart_bar_item{
    width: 47px;
    height: 47px;
    background: white;
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
}