.active_line{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 120px;
    margin: 0 auto;
}
.active_line_container{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 120px;
    height: 100%;
    touch-action: none;
    background-color: green;
}
.active_line_item{
    height: 4px;
    width: 100%;
    background-color: #fff;
    border-radius: 20px;
    touch-action: none;
}